<template>
  <v-row justify="center" v-if="dialog">
    <v-dialog v-model="dialog" persistent max-width="350">
      <v-card 
        class="confirm-dialog" 
        light
      >
        <div class="px-4 pt-2">
        <v-row>
          <v-col class="py-0">
            <v-card-title class="headline pa-0">
              {{ title }}
            </v-card-title>
          </v-col>
          <v-col class="text-right py-0" cols="auto">
            <v-btn
              class="px-0"
              :min-width="20"
              text
              @click="send('cancel')"
            >
              <v-icon>close</v-icon>
            </v-btn>
          </v-col>
        </v-row>
        </div>
        
        <v-card-text v-if="description">
          {{ description }}
        </v-card-text>
        <v-card-text class="pt-2 pb-0">
          <slot></slot>
        </v-card-text>
        <v-card-actions class="px-4 pb-4">
          <v-row>
            <v-spacer></v-spacer>
            <v-col cols="12" sm="auto" class="text-center pa-0">
              <btn
              class="btn-confirm"
              :block="false"
              :disabled="btnDisabled"
              @clicked="send('confirm')"
              text="Confirmar"
            />
            </v-col>
            <v-col cols="12" sm="auto" class="text-center py-0 pt-2 pt-sm-0">
              <btn-text 
              class="btn-cancel"
              :block="false"
              text="Cancelar"
              @clicked="send('cancel')"
            />
            </v-col>
          </v-row>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import Btn from '@/components/globais/buttons/secondary/BtnSecondary'
import BtnText from '@/components/globais/buttons/text/BtnText'

export default {
  name: 'Default',
  components: { Btn, BtnText },
  props: {
    btnDisabled: { default: false },
    title: { required: true },
    description: { required: false },
    danger: { default: false }
  },
  data: () => ({
    dialog: false
  }),
  methods: {
    send (emit) {
      this.dialog = false
      this.$emit(emit)
    },
    showDialog () {
      this.dialog = true
    }
  }
}
</script>