<template>
  <default-dialog
    ref="dialog"
    title="Recusar agendamento?"
    :btnDisabled="disabled"
    @confirm="confirm"
  >
    <textarea-field
      label="Informe o motivo do cancelamento"
      @onKeyUp="changeData"
    />
  </default-dialog>
</template>

<script>
import DefaultDialog from '@/components/globais/dialogs/default/Default'
import TextareaField from '@/components/globais/form/textarea-field/TextareaField'

export default {
  components: { DefaultDialog, TextareaField },
  data: () => ({
    cancellationReason: null,
    schedule: null
  }),
  computed: {
    disabled () {
      return !this.cancellationReason || this.cancellationReason.length < 2 || this.cancellationReason.length > 250
    }
  },
  methods: {
    changeData (val) {
      this.cancellationReason = val
    },
    confirm () {
      this.$emit('onConfirmRefuse', this.schedule, this.cancellationReason)
    },
    show (schedule) {
      this.cancellationReason = ''
      this.schedule = schedule
      this.$refs.dialog.showDialog()
    }
  }
}
</script>